.main{
    cursor: pointer;
    text-decoration: dotted;
    padding:5px;
    border-bottom: 2px dotted black;
    position: relative;
}

.tooltip{
    position: absolute;
    background-color: black;
    color: white;
    width: max-content;
    text-align: center;
    padding: 5px 10px;
    z-index: 1;
}

.top{
    top: -30px;
    transform: translateX(-30%);
}

.top::before{
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background-color: black;
    bottom: -4px;
    left: 120px;
    transform: rotate(45deg);
    z-index: -1;
}

.left{
    left: 110%;
    top: 20%;
}

.left::before{
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background-color: black;
    left: -4px;
    top: 12px;
    transform: rotate(45deg);
    z-index: -1;
}

.bottom{
    bottom: -45px;
    transform: translateX(-30%);
}

.bottom::before{
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background-color: black;
    top: -4px;
    left: 120px;
    transform: rotate(45deg);
    z-index: -1;
}

.right{
    right: 110%;
    top: 20%;
}

.right::after{
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background-color: black;
    right: -4px;
    top: 12px;
    transform: rotate(45deg);
    z-index: -1;
}